___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

header {
    background-color: #1F2937;
    font-family: Roboto;
    color: white;
    font-size: 36px;
    font-weight: 900;
    text-align: center;
}

h1 {
    margin: 0px;
}

#add {
    background-color: darkgrey;
    display: flex;
    gap: 45px;
    justify-content: center;
    align-items: center;
}

button {
    background-color: #1F2937;
    font-family: Roboto;
    color: white;
    border-radius: 17px;
    margin-block: 11px;    
}

button:hover {
    background-color: #374151;
    cursor: pointer;
}

#add button {
    padding: 0.5em 1em;
    font-size: 0.9rem;
}

table {
    border-collapse: collapse;
    border: 2px solid white;
    letter-spacing: 1px;
    font-size: 0.8rem;
    background-color: #1F2937; /* match buttons */
    color: white;
    margin: 2rem auto;
    width: 90%;
    max-width: 1000px;
    text-align: center;
}

table caption {
    color: white;
    font-size: 1.8rem;
    font-weight: bold;
    padding: 10px;
}


td, th {
    border: 1px solid white;
    padding: 10px 20px;
}

td {
    text-align: center;
}

form {
    display: flex;
    flex-direction: column;
    gap: 17px;
}

.form-element {
    display: flex;
    flex-direction: column;
}

.project-card {
    border: 1px solid #ccc;
    padding: 1em;
    margin: 1em 0;
    border-radius: 8px;
    background-color: #f9f9f9;
}

.project-card h2 {
    margin: 0 0 0.5em;
}

@media (max-width: 600px) {
    form {
        padding: 1em;
    }

    .form-element input, .form-element select {
        font-size: 1em;
    }

    table, th, td {
        font-size: 0.65rem;
        padding: 5px;
    }

    .project-card {
        padding: 0.5em;
        margin: 0.5em;
    }

    #add {
        flex-direction: column;
        gap: 15px;
    }
}

dialog {
    border: none;
    border-radius: 12px;
    padding: 2em;
    background: white;
    color: black;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
    width: 90%;
    max-width: 400px;
}

#content {
    background-color: #111827;
    padding: 2rem;
    min-height: 100vh;
    box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.05);
}

